@tailwind base;
@tailwind components;
@tailwind utilities;
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
#Main{
    padding-top: 10px;
    height: 538px;
    display: grid;
    object-fit: center;
}
.carousel-image-container {
    display: flex;
    justify-content: center;
  }
  
  .carousel-image {
    margin: 0 auto;
    max-height: 500px; /* Adjust the height as needed */
  }
  
.product-card {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 20px;
    margin-bottom: 20px;
  }
  
  .product-card h3 {
    margin-top: 0;
  }
  
  .product-card label {
    font-weight: bold;
  }
  
  .product-card input,
  .product-card textarea {
    width: 100%;
    padding: 5px;
    margin-bottom: 10px;
  }
  
  .product-card button {
    margin-right: 10px;
  }
  
  .btn-save {
    background-color: green;
    color: white;
  }
  
  .btn-edit {
    background-color: blue;
    color: white;
  }
  
  .btn-cancel {
    background-color: orange;
    color: white;
  }
  
  .btn-close {
    background-color: red;
    color: white;
  }
  
.list-products {
    width: 100%;
  }
  
  .list-products h2 {
    margin-top: 0;
    margin-bottom: 20px;
  }
  
  .list-products table {
    width: 100%;
  }
  
  .list-products th,
  .list-products td {
    padding: 10px;
    text-align: left;
  }
  
  .list-products th {
    background-color: #f2f2f2;
    font-weight: bold;
  }
  
  .list-products tbody tr:nth-child(even) {
    background-color: #f9f9f9;
  }
  
  .list-products .product-row {
    display: flex;
    align-items: center;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  
  .list-products .product-row .product-info {
    flex-grow: 1;
    padding: 10px;
  }
  
  .list-products .product-row .product-actions {
    padding: 10px;
  }
  
  .list-products .product-row .product-actions button {
    margin-right: 5px;
  }
  
  @media screen and (max-width: 768px) {
    .list-products table {
      width: 100%;
      display: block;
      overflow-x: auto;
    }
    
    .list-products thead {
      display: none;
    }
    
    .list-products tbody tr {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 10px;
      border: 1px solid #ccc;
      border-radius: 5px;
      padding: 10px;
    }
    
    .list-products tbody td {
      width: 50%;
    }
  }
  
.product-form {
  margin-top: 20px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f5f5f5;
  width: 100%;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
}

.product-form h2 {
  margin-bottom: 10px;
}

.product-form form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.product-form div {
  margin-bottom: 10px;
}

.product-form label {
  margin-right: 10px;
}

.product-form input[type="text"],
.product-form input[type="checkbox"] {
  padding: 5px;
}

.product-form button[type="submit"] {
  padding: 8px 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.product-form button[type="submit"]:hover {
  background-color: #0056b3;
}

#spaceContainer {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  #usercontainer {
    width: 100%;
  }
  
  .left-menu-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
  }
  
  .left-menu-header button {
    padding: 0;
    background-color: transparent;
    border: none;
    cursor: pointer;
  }
  
  .left-menu-body {
    background-color: #f8f9fa;
    padding: 10px;
    height: calc(100vh - 78px);
    transition: all 0.3s ease;
    overflow-y: auto;
  }
  
  .left-menu-body p {
    margin-bottom: 0;
  }
  
  .workspace {
    height: calc(100vh - 78px);
    background-color: #ffffff;
    padding: 10px;
    transition: all 0.3s ease;
    overflow-y: auto;
    flex: 1 1; /* Added */
  }
  
  .workspace.collapsed {
    margin-left: 0; /* Added */
  }
  
  .list-products {
    transition: all 0.3s ease;
    height: 100%; /* Added */
  }
  
  .list-products.collapsed {
    display: none; /* Added */
  }
  
  @media (max-width: 576px) {
    .row {
      flex-direction: column;
    }
  
    .col-sm-4 {
      width: 100%;
    }
  
    .col-sm-8 {
      width: 100%;
      margin-top: 20px;
    }
  }
  
  @media (min-width: 576px) {
    .col-sm-4.collapsed {
      width: 0;
      padding: 0;
      overflow: hidden;
    }
  
    .col-sm-8.expanded {
      width: 100%;
      padding-left: 0;
      padding-right: 0;
    }
  
    .col-sm-8.expanded .container-fluid {
      padding-left: 0;
      padding-right: 0;
    }
  
    .col-sm-8.expanded .row {
      margin-left: 0;
      margin-right: 0;
    }
  
    .col-sm-8.expanded .col-sm-8 {
      flex: 1 1;
    }
  }
  
#mainContainer{
    display: grip;
    place-items: center;
    max-width: 350px;
    height: 500px;
    margin-top: 50px;
    box-shadow: 0 2px 5px 0 rgb(0 0 0 / 16%), 0 2px 10px 0 rgb(0 0 0 / 12%);
    justify-content: center;

}
img{
    padding-bottom: 20px;
}
